.reviews,
.reviews__text-block {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.reviews {
  background-image: url(../img/cite_bg.jpg);
  padding: 32px 16px;
  @media screen and (min-width: 1024px) {
    padding: 100px 64px 96px;
  }
  @media screen and (min-width: 1140px) {
    padding: 100px 180px 96px;
  }
  @media screen and (min-width: 1920px) {
    padding: 100px 380px 96px;
  }
  @media screen and (min-width: 2560px) {
    max-width: 1920px;
    margin: 0 auto;
  }
  &__title {
    color: #fff;
  }
  &__text-block {
    margin-top: 72px;
  }
  &__quotes {
    margin-bottom: 32px;
  }
  &__text-block_cite {
    color: #bbbbbb;
    font-size: 1em; //16px
    line-height: 1.5em; //24px
    text-align: center;
    margin-bottom: 32px;
  }
  &__text-block_name {
    font-size: 1em; //16px
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 12px;
  }
  &__text-block_position {
    font-size: 0.8125em; //13px
    color: #bbbbbb;
  }
  &__photo {
    display: block;
    width: 162px;
    overflow: hidden;
    margin-top: 24px;
  }
  &__photo-main {
    display: block;
    width: 100%;
    border-radius: 50%;
    border: 10px solid rgba(#1fdab5, $alpha: 0.2);
  }
}

.portrait {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 32px;
  @media screen and (min-width: 768px) {
    justify-content: center;
  }
  &__btn-left,
  &__btn-rigth {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border: 1px solid #bbbbbb;
    background: transparent;
    padding-left: 4px;
    cursor: pointer;
  }
  &__btn-rigth {
    padding: 0 4px 0 0;
    @media screen and (min-width: 768px) {
      margin-left: 24px;
    }
  }
  &__btn-left {
    @media screen and (min-width: 768px) {
      margin-right: 24px;
    }
  }
  &__btn_arrow-left,
  &__btn_arrow-rigth {
    display: block;
    width: 10px;
    height: 10px;
    border-left: 2px solid #bbbbbb;
    border-top: 2px solid #bbbbbb;
    transform: rotate(-45deg);
  }
  &__btn_arrow-rigth {
    transform: rotate(135deg);
  }
  &__img-block {
    display: block;
    width: 52px;
    overflow: hidden;
    @media screen and (min-width: 768px) {
      margin: 0 8px;
    }
  }
  &__img-block:hover {
    transform: translateY(-10px);
  }

  &__mini {
    display: block;
    width: 100%;
    border-radius: 50%;
    border: 4px solid rgba(#1fdab5, $alpha: 0.2);
  }
}

.btn_arrow:hover {
  background: #18cfab;
  border: none;
}

.hide {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
  }
}
