.news {
  padding: 32px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  @media screen and (min-width: 1440px) {
    padding: 74px 0 130px;
  }
  &__block {
    width: 100%;
    margin-top: 56px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    @media screen and (min-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
      padding: 0 56px;
    }
    @media screen and (min-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
      padding: 0 200px;
      margin-top: 68px;
    }
    @media screen and (min-width: 1440px) {
      grid-template-columns: repeat(4, 1fr);
      padding: 0 144px;
    }
    @media screen and (min-width: 1920px) {
      padding: 0 380px;
    }
    @media screen and (min-width: 2560px) {
      max-width: 1920px;
      padding: 0 380px;
    }
  }
  &__block_item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    text-align: center;
    position: relative;
    padding: 0 16px;
    margin-bottom: 24px;
  }
  &__block_img {
    font-size: 0;
    width: 100%;
    display: block;
  }
  &__block_title {
    display: block;
    font-size: 1.125em; //18px
    font-weight: bold;
    color: #18cfab;
    margin-top: 16px;
  }
  &__block_text {
    display: flex;
    justify-content: center;
    margin-top: 8px;
  }
  &__block_text_admin,
  &__block_text_comment {
    font-size: 0.875em; //14px
    text-align: center;
    color: #777777;
    font-weight: 300;
    padding: 0 12px;
    text-decoration: none;
  }
  &__block_divider {
    display: block;
    width: 1px;
    height: 16px;
    background-color: #bebebe;
  }
  &__block_badge {
    position: absolute;
    top: 0;
    right: 16px;
    display: block;
    width: 70px;
    background-color: #203e38;
    padding: 8px 0;
  }
  &__block_badge_date,
  &__block_badge_month {
    display: block;
    text-align: center;
    color: #fff;
    font-style: 1em; // 16px
    line-height: 1.5em; // 24px
  }
}

.news__block_img:hover ~ .news__block_badge {
  background-color: #18cfab;
}
