.gallery {
  width: 100%;
  padding: 32px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media screen and (min-width: 1024px) {
    padding: 107px 16px 67px;
  }
  &__grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    gap: 20px;
    margin-top: 64px;
    @media screen and (min-width: 768px) {
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: (161px 52px 36px 32px 43px 123px 18px);
    }
    @media screen and (min-width: 1024px) {
      grid-template-rows: (221px 78px 56px 47px 64px 159px 42px);
      margin: 92px auto 0;
    }
    @media screen and (min-width: 1440px) {
      max-width: 1160px;
      grid-template-rows: (260px 94px 71px 55px 78px 188px 52px);
    }
  }
  &__img {
    display: block;
    width: 100%;
  }
  &__img-box {
    width: 100%;
    display: block;
  }
}

.gallery__img-box:nth-child(1) {
  grid-row: span 1;
}
.gallery__img-box:nth-child(2) {
  grid-row: span 2;
}
.gallery__img-box:nth-child(3) {
  grid-row: span 3;
}
.gallery__img-box:nth-child(4) {
  grid-row: span 3;
}
.gallery__img-box:nth-child(5) {
  grid-row: span 3;
}
.gallery__img-box:nth-child(6) {
  grid-row: span 2;
}
.gallery__img-box:nth-child(7) {
  grid-row: span 3;
}
.gallery__img-box:nth-child(8) {
  grid-row: span 3;
}
